"use client"
import React from "react";
import Image from "next/image";
import { useState, useEffect } from "react";

const Company = (props) => {
  const [hover, setHover] = useState(false);

  if (hover) {
    return (
      <div className={"bg-gray-800 text-white rounded-lg p-2 h-56 w-3/12 m-1"} id="company" onMouseEnter={() => setHover(true)} onMouseLeave={() => setHover(false)}>
        <h1 className="text-2xl">{props.companyName}</h1>
        <span className="ml-2">
          <div className="text-lg mb-3">{props.companyDescription}</div>
          <div className="text-lg">{"\""+props.companyFeedback+"\""}</div>
        </span>
      </div>
    );
  }
  return (
    <div className={"bg-gray-800 text-black rounded-lg p-2 h-56 w-3/12 m-1 flex items-center justify-center"} id="company" onMouseEnter={() => setHover(true)} onMouseLeave={() => setHover(false)}>
      <Image src={props.companyLogo} alt="Company logo" height={100} width={200} className=""/>
    </div>
  );
}

export default Company;